/* src/App.css */

/* Global Variables for Spotlight */
:root {
  --spotlight-size: 300px; /* Adjust the spotlight size as needed */
  --spotlight-color: rgba(255, 255, 255, 0.8); /* Light color for spotlight */
}

/* Apply black background and hide default cursor */
.App {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
  flex-direction: column;
  color: white;
  background-color: black; /* Make the background completely black */
}

/* Animated Spotlight Effect */
.intro-heading {
  font-size: 4rem;
  font-weight: bold;
  color: transparent; /* Hidden by default */
  position: relative;
  text-align: center;
  background-image: linear-gradient(
    120deg,
    transparent 0%,
    var(--spotlight-color) 50%,
    transparent 100%
  );
  background-repeat: no-repeat;
  background-size: 200% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  animation: spotlight-reveal 5s linear infinite; /* Continuous left-to-right reveal */
}

@keyframes spotlight-reveal {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

.rotating-mandala {
  width: 400px;
  height: 400px;
  animation: spin 20s linear infinite;
  border-radius: 50%;
  filter: drop-shadow(0 0 10px rgba(255, 255, 255, 0.2));
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
